<template>
  <div>
    <div class="miniheader">
      <div class="container">
        <div class="miniheader-logo">
          <a href="#">小米官网</a>
        </div>
        <div class="miniheader-title">
          <h2>支付订单</h2>
          <p>温馨提示：产品是否购买成功，以最终下单为准哦，请尽快结算</p>
        </div>
        <div class="miniheader-info">
          <a href="#" class="user-name">
            <span></span>
          </a>
          <span class="sep">|</span>
          <a href="#" class="info-order">我的订单</a>
        </div>
      </div>
    </div>

    <div class="confirm">
      <div class="container">
        <form action="post">
          <div class="confirm-order confirm-section clearfix">
            <i class="iconfont">&#x221a;</i>
            <div class="box-order clearfix">
              <div class="order-info">
                <h2 class="info-title">订单提交成功！去付款咯～</h2>
                <p class="info-time">
                  请在
                  <span>0小时30分</span>内完成支付, 超时后将取消订单
                </p>
                <p class="info-address">
                  收货信息：
                  <span class="address">{{receiveInfo}}</span>
                </p>
              </div>
              <div class="order-total">
                <p>
                  应付总额：
                  <span>
                    <em class="money">{{totalOrderGoodsPrice}}</em>元
                  </span>
                </p>
                <a href="javascript:;">订单详细</a>
              </div>
            </div>

            <div class="order-detail">
              <ul>
                <li class="clearfix">
                  <div class="label">订单号：</div>
                  <div class="content order-no">{{order_no}}</div>
                </li>
                <li class="clearfix">
                  <div class="label">收货信息：</div>
                  <div class="content address">{{receiveInfo}}</div>
                </li>
                <li class="clearfix">
                  <div class="label">商品名称：</div>
                  <div class="content items">
                    <p
                      v-for="(goods,index) in orderGoods"
                      :key="`goods_${index}`"
                    >{{`${goods.name} × ${goods.quantity}`}}</p>
                  </div>
                </li>
              </ul>
            </div>
          </div>

          <div class="confirm-payment confirm-section">
            <div class="payment-title">选择以下支付方式付款</div>
            <div class="payment-list">
              <ul class="clearfix">
                <li id="pay" @click="pay">
                  <img src="/weixinpay.png" alt />
                </li>
                <!-- <li><img src="img/payOnline_zfb.png" alt=""></li> -->
                <!-- <li><img src="/unionpay.png" alt=""></li> -->
              </ul>
            </div>
          </div>
        </form>
      </div>

      <div class="confirm-qrcode" v-show="isShow_pay">
        <div id="qrcode">
          <img :src="payURL" alt />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "../utils/api";

export default {
  created() {
    document.title = "支付订单";
  },

  data() {
    return {
      order_no: this.$route.query.order_no,
      address: JSON.parse(this.$route.query.address),
      orderGoods: JSON.parse(this.$route.query.orderGoods),
      totalOrderGoodsPrice: this.$route.query.totalOrderGoodsPrice,
      isShow_pay: false,
      payURL: ""
    };
  },
  watch: {},
  computed: {
    receiveInfo() {
      return (
        this.address.recipient +
        " " +
        this.address.phone +
        " " +
        this.address.area +
        " " +
        this.address.address
      );
    }
  },
  methods: {
    pay() {
      api.post("/user/pay", { order_no: this.order_no }).then(data => {
        this.payURL = data;
      });
    },
    
  }
};
</script>

<style lang="scss" scoped>
@import url(../css/iconfont.css);
@import url(../css/style.css);
@import url(../css/confirm.css);
</style>